import React from 'react';
import './DataTable.scss';
import Checkbox from './Checkbox/Checkbox';
import ColumnHeader from './ColumnHeader/ColumnHeader';


export default function DataTable(props) {
  const [active, setActive] = React.useState('active');
  const [active2, setActive2] = React.useState('');
  const [sort, setSort] = React.useState(false);
  return (
    <React.Fragment>
      <div className="data-table__spacer" />
      <table className="data-table">
        <thead>
          <tr>
            <th style={{ width: '2.7%' }}><Checkbox checked="checked" /></th>
            <th><ColumnHeader  title="Date" sortable={true} /></th>
            <th>Payee</th>
            <th>Description</th>
            <th>Type</th>
            <th>Selection</th>
            <th>Reference</th>
            <th>VAT</th>
            <th>Spent</th>
            <th>Received</th>
            <th>REC?</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Checkbox</td>
            <td>date</td>
            <td>Payee</td>
            <td>Description</td>
            <td>Type</td>
            <td>Selection</td>
            <td>Reference</td>
            <td>VAT</td>
            <td>Spent</td>
            <td>Received</td>
            <td>REC?</td>
            <td>Action</td>
          </tr>
        </tbody>
      </table>
    </React.Fragment>
  );
}
